#cropper{
  &_inner{
    margin:2rem;
    max-width: 80% ;
    max-height: 80%;
  }
  &_header{
    display: flex;
    justify-content: space-around;
    margin: 0 1rem ;
    button:first-child{
      border: none;
      font-size: 1.3rem;
      background-color: initial;
    }
    button:first-child:hover{
      color: black;
      background-color: rgb(235,235,235);
    }
    button:last-child{
      background-color: rgb(52, 53, 54);
      color: #ffff;
      font-size: 1rem;
      margin: 3px 0;
      padding: 0 10px;
    }
    div{
      font-size: 1.2rem;
      margin-top: 0.5rem;
    }
  }
}
#cropper_crop{
  border-radius: 10px ;
  max-width: 100%;
  max-height:calc(calc(100% - 5rem) * 0.7);
  margin: 15%;
  display: flex;
  justify-content: center;
  align-items: center;

}
#cropper{
  .ReactCrop__image{
    max-width: calc(50vw * 0.9);
    max-height: calc((100vh * 0.7 - 5rem) * 0.8);
    width: auto;
    height: auto;
  
  }
}


@media screen and (min-width:500px) {
  #cropper_header{
    width: 90%;
    justify-content: space-between;
  }
}
@media screen and (min-width:720px) {
  .cropper{
    &.factory #cropper_crop {
      margin: 8%;
    }
  }
  #cropper_crop{
    margin:0;
  }
  #cropper_inner {
    padding:2rem;
    border-radius: 20px;
  }

}
@media screen and (min-width:1440px) {
  #cropper_header{
    div{
      font-size: 1.5rem;
      margin-top: 10px;
    }
    button:first-child{
      font-size: 1.8rem;
    }
    button:last-child{
      font-size: 1.5rem;
      margin: 8px 0;
      padding: 0 1rem;
    }
  } 
  .cropper.factory #cropper_crop {
    margin: 2rem;
  }
}

