
@import './common' ;

#susp{
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  .inner{
    width: 80%;
    height: fit-content;
    padding:1.2rem;
    box-sizing: border-box;
    background-color: rgba(235, 235, 235, 0.753);
    border-radius: 1rem;
  }
  .closeBtn_susp {
    margin-top: 10px;
    padding: 0;
    width: 1.2rem;
    height: 1.2rem;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color:inherit;
    
    svg {
      font-size: 1rem;
      margin-bottom :  0; 
      margin-left: 0;
      padding:0;
      width: 100%;
      height: 100%;
    }
    &:hover{
      background-color: black;
      padding:0;
      svg{
        fill: #ffff;
      }
    }
  }
  .notice{
    margin-top: 10px;
    text-align: left;
    width: 100%;
    overflow-wrap: break-word;
    font : {
      weight: normal;
      size: 1rem
    };
  }
  a{
    border-bottom: 1px solid black;
  }
}
